.forgotPassword{
    margin-top:50px;
    position:relative;
}
.forgotPassword>img{
    position: absolute;
    top:-140px;
    left:0;
}
.forgotPassword>img:hover{
    -webkit-transform: scale(1.5);
        -ms-transform: scale(1.5);
            transform: scale(1.5)
}
.forgotPassword>.row>label{
    font-size: 16px;
    color: #ccc;
}
.forgotPassword>.row>input{
    border:none;
    border:2px solid #8ae2ff;
    border-radius: 4px;
}
.forgotPassword>.actions{
    margin:20px auto;
}


